.descriptionbox {
    margin: 120px 130px;
}
.desc-show {
    display: flex;
}
.desc-show-box {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    font-weight: 600;
    width: 131px;
    height: 70px;
    border: 1px solid #d0d0d0;
}
.desc-show-box.fade {
    color: #555;
    background: #fbfbfb;
}
.descriptionbox-desc {
    display: flex;
    flex-direction: column;
    gap: 25px;
    border: 1px solid #d0d0d0;
    padding: 46px;
    padding-bottom: 70px;
}